.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  height: 100%;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
main{
  padding-top: 50px;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
  background-color: #F2F4F8;
}

.option-icons{
  opacity: 0;
  transition-duration: 0.3s;
  cursor: pointer;
}

.table-infos:hover .option-icons{
  opacity: 1;
}